import noData from '@/components/noData'
import back from '@/components/back'
import { setInterval } from 'timers';

export default {
    components:{
        noData,
        back
    },
    data(){
        return{
			inputFlag:false,
			search: '',
			page: 1,
			user: this.$store.state.userInfo,
			searchList:[],
			haveMore: true,
			Medication:null,
			tText:[
				{'id':1,text:'饮片'},
				{'id':2,text:'颗粒'},
			],
			subId:this.$store.state.chState?this.$store.state.chState:1,//判断是否是饮片或者颗粒
			selShow:false,
			selName:null,
			Specification:null,
			ListDetails: [],
			chooseL:null,
			PackageQuantity:null,//颗粒包装数量
			Quantity:null,//饮片包装数量
			ProcesseDemand:null, //煎煮特殊要求
			PackageCount:null,//包装单位
			ProcesseDemandId:null,
			subTabShow:false,
			showConfirm: false,
			chooseO:null,
			list: [],
			quantity:1,
			quantityArr:[],//
			quantityText:null,
			categoryItem:null,
			act:null,
			act2:null,
			num:null,
			modifyState:1,
			modifyIdx:null,
			showConfirm2: false,
			popIdx:null,
			showConfirm3: false,
			showConfirm4: false,
			showConfirm5: false,
			MedicationCompanyId:null,
			checkIdshow:1,
			ldetailText:null,
			isNo:0,
			orderInfo: this.$store.state.pInfo,
			TCMTempList:[],
			Swtich:this.$route.query.Swtich,
			Diagnoses:''
        }
    },
    watch: {
        activeTab(){
            this.page = 1
            this.haveMore = true
            this.pageShow = false
          // this.pull()
        }
    },
    computed:{
        totalPrice(){
            let p = 0
			if(this.ListDetails){
				if(this.ListDetails.length){
				    this.ListDetails.forEach(v => {
						if(v.MedicationStatus != 0 && v.MedicationStatus){
							p += 0
						}else{
							if(this.subId == 2){
								p += (+v.Price) * v.PackageQuantity
							}else{
								if(v.IsRetail == 1){
									p += (+v.Price) * v.Quantity
								}else{ 
									if(Number(v.Quantity) > v.PackageCount){
										p += (Math.ceil(v.Quantity / v.PackageCount) * v.PackageCount) * v.Price
									}else{
										p += (+v.Price) * v.PackageCount
									}
								}
							}
						}
				    })
				}
			}
            return p.toFixed(2)
        },
		totalp(){
		    let p = 0
			if(this.ListDetails){
				if(this.ListDetails.length){
				    this.ListDetails.forEach(v => {
						if(this.subId == 2){
							p += Number(v.Quantity)
						}else{
							if(v.DosageUnit == '克'){
								p += Number(v.Quantity)
							}
						}
				    })
				}
			}
		    return p
		}
    },
    created(){
		const {chList} = this.$store.state
		this.ListDetails = chList
		this.getMedication()
		 if(!this.$route.query.cid){
			this.checkIdshow = 1
		}else{
			this.checkIdshow = 2
			this.GetTCMTempPrescription()
		}
    },
    mounted(){
		this.pullProcesseDemand()
		//this.isJudge()
		this.ldetail()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
		cancelConfirm5(){
			this.$store.dispatch('pushsaveInfo', null)
			this.$store.dispatch('pushchList', [])
			this.$store.dispatch('pushchState', 1)
			this.$router.push('/chooseDrugs')
		},
		confirm5(){
			let arr ={
				Contraindication: this.TCMTempList.Contraindication,
				ContraindicationId: this.TCMTempList.Contraindication,
				DayDosage: this.TCMTempList.DayDosage,
				Diagnoses: this.TCMTempList.Diagnoses,
				TimesDosage: this.TCMTempList.TimesDosage,
				TotalDosage: this.TCMTempList.TotalDosage,
				UsageMethod: this.TCMTempList.UsageMethod,
				UsageMethodId: this.TCMTempList.UsageMethodId,
			}
			this.$store.dispatch('pushsaveInfo', arr)
			this.$store.dispatch('pushchList', this.TCMTempList.ListDetails)
			this.$store.dispatch('pushchState', this.TCMTempList.MedicationForm)
			this.$router.push('/cM/prescription')
		},
		GetTCMTempPrescription(){
			this.$post('Prescription/Prescription/GetTCMTempPrescription',{
			    DoctorId: this.user.Data.User.DoctorId,
				PatientId:this.orderInfo.PatientInfo.PatientId
			}).then(o => {
				if(o){
					this.showConfirm5 = true
					this.TCMTempList = o
				}
			})
		},
		subTabShowMethods(){
			if(this.subTabShow){
				this.subTabShow = false
			}else{
				this.subTabShow = true
			}
		},
		getMedication(){ //获取供药单位
			this.$get('Prescription/Pharmacy/GetMedicationCompanyByDrId',{
			    DoctorId: this.user.Data.User.DoctorId,
				MedicationType:2
			}).then(o => {
			    this.Medication = o.Name
				this.MedicationCompanyId = o.Id
			})
		},
		pullProcesseDemand(){
			this.$post('PlatFormAPI/Category/QueryCategoryItem',{
			    CategoryIds:54
			}).then(o => {
			    this.categoryItem = o[0].Items
			})
		},
		isJudge(i){
			this.quantityText = i.PackageUnit
			if(this.subId == 2 && i.PackageUnit != "克"){
				this.quantityArr = ["1","2","3","4"]
				this.num = 1
			}else{
				this.quantityArr = ["5","10","15","20"]
				this.num = 5
			}
		},
		ldetail(){
			let ldetailArr = []
			this.ListDetails.forEach(v => {
				if(v.MedicationStatus != 0 && v.MedicationStatus){
					ldetailArr.push(v.Name)
				}
			})
			if(ldetailArr && ldetailArr.length){
				this.isNo = 1
			}else{
				this.isNo = 0
			}
			this.ldetailText = ldetailArr.join(',')
		},
		tabClick(o){
			this.chooseO = o
			if(this.ListDetails && this.ListDetails.length && this.subId != o.id){
				this.showConfirm = true
			}else{
				this.search = ''
				this.subId = o.id
				this.$store.dispatch('pushchState', this.subId)
				this.$store.dispatch('pushsaveInfo', null)
				//this.isJudge()
			}
		},
		confirm(){
			this.search = ''
			this.subId = this.chooseO.id
			this.$store.dispatch('pushchState', this.subId)
			this.ListDetails = []
			this.$store.dispatch('pushchList', [])
			//this.isJudge()
			this.$store.dispatch('pushsaveInfo', null)
		},
		kong(){
			this.showConfirm3 = true
		},
		confirm3(){
			this.search = ''
			this.ListDetails = []
			this.$store.dispatch('pushchList', [])
		},
		choosedrugs(i){
			var t = 0
			this.ListDetails.forEach(v => {
				 if(v.MedicationId == i.MedicationId){
					 t = 1
				 }
			})
			if(t==1){
				this.$toast('您已添加了该药品')
				return 
			}
			this.selShow = true
			this.selName = i.Name
			this.Specification = i.Specification
			this.quantity = 1
			this.ProcesseDemand = ''
			this.act = 100
			this.act2 = 100
			this.chooseL = i
			this.isJudge(i)
		},
		modify(o,idx){
			this.selShow = true
			this.categoryItem.forEach((v,k) => {
				 if(v.CategoryItemName == o.ProcesseDemand){
					this.act2 = k
				 }
			})
			this.act = 100
			if(this.subId == 2){
				this.quantity = o.PackageQuantity
			}else{
				this.quantity = o.Quantity
				if(!o.ProcesseDemand){
					this.act2 = 100
				}
				this.ProcesseDemand = o.ProcesseDemand
			}
			this.modifyState = 2
			this.modifyIdx = idx
			this.chooseL = o
			this.isJudge(o)
		},
		yesConfirm(){
			if(this.subId == 2){
				this.chooseL.Quantity = Math.floor(this.quantity * this.chooseL.PackageCount)
				this.chooseL.PackageQuantity =  Math.floor(this.quantity)
			}else{
				this.chooseL.Quantity = Math.floor(this.quantity)
				this.chooseL.PackageQuantity = Math.floor(this.quantity / this.chooseL.PackageCount)
			}
			if(Math.floor(this.quantity) < 1){
				this.$toast('数量需大于1')
				return
			}
			this.chooseL.ProcesseDemand = this.ProcesseDemand
			this.chooseL.ProcesseDemandId = this.ProcesseDemandId
			if(this.modifyState == 2){
				 this.ListDetails.splice(this.modifyIdx, 1, this.chooseL)
			}else{
				this.ListDetails.push(this.chooseL)
			}
			console.log(this.ListDetails)
			this.$store.dispatch('pushchList', this.ListDetails)
			this.search = ''
			this.selShow = false
			this.modifyState = 1
		},
		usBoutique(){
			this.showConfirm4 = true
		},
		confirm4(){
			const StandardCode = []
			this.ListDetails.forEach(v => {
				StandardCode.push(v.StandardCode)
			})
			this.$post('Prescription/Medication/GetTCMMedicationOfGreate',{
			    MedicationCompanyId: this.MedicationCompanyId,
				StandardCodeList:StandardCode
			}).then(o => {
				this.ListDetails.forEach((a,k) => {
					o.forEach(b => {
						if(a.StandardCode == b.StandardCode){
							if(a.Grade < b.Grade){
								this.ListDetails.splice(k, 1, b)
								if(this.subId == 2){
									this.ListDetails[k].PackageQuantity =  a.PackageQuantity
								}else{
									this.ListDetails[k].ProcesseDemand = a.ProcesseDemand
									this.ListDetails[k].ProcesseDemandId = a.ProcesseDemandId
									this.ListDetails[k].Quantity = a.Quantity
								}
							}
						}
					})
				})
			})
			//this.$store.dispatch('pushchList', this.ListDetails)
		},
		noConfirm(){
			this.selShow = false
		},
		complete(){
			if(this.isNo == 1){
				 return this.$toast(this.ldetailText+' 药品库存不足，请删除该药品')
			}
			if(this.ListDetails && this.ListDetails.length){
				this.$router.push('/cM/prescription')
			}else{
				this.$toast('请先添加药材')
			}
		},
		saveSwtich(){
			if(!this.Diagnoses){
                return this.$toast('请填写临床诊断')
            }
			if(this.isNo == 1){
				 return this.$toast(this.ldetailText+' 药品库存不足，请删除该药品')
			}
			console.log(this.ListDetails)
			if(this.ListDetails && this.ListDetails.length){
				this.$post('Prescription/Prescription/SaveTCMPrescriptionOftenUsed',{
					Id:0,//	自增Id
					Name:'',//	处方名称
					DoctorId:this.user.Data.User.DoctorId,
					MedicationCompanyId:this.MedicationCompanyId,
					OrgId:this.user.Data.User.OrgId,
					Diagnoses:this.Diagnoses,
					MedicationForm:this.subId,
					ListDetails:this.ListDetails,	
				}).then(o => {
					this.$toast('保存成功')
					this.$router.push('/allPrescription?Refresh=1')
				})
			}else{
				this.$toast('请先添加药材')
			}
		},
		selquantity(a,idx){
			this.act = idx
			this.quantity = a
		},
		CategoryItem(o,idx){
			this.act2 = idx
			this.ProcesseDemand = o.CategoryItemName
			this.ProcesseDemandId = o.CategoryItemValue
		},
		add(){
			this.act = 100
			if(this.subId == 2 && this.quantityText != "克"){
				
				this.quantity = Math.floor(this.quantity) + 1
			}else{
				this.quantity = Math.floor(this.quantity) + 5
			}
		},
		reduce(){
			if(this.quantity > 1 ){
				this.quantity = Math.floor(this.quantity) -1
			}
			this.act = 100
		},
		pop(idx){
		    this.showConfirm2 = true
		    this.popIdx = idx
		},
		confirm2(){
		    this.ListDetails.splice(this.popIdx, 1)
			this.ldetail()
			this.$store.dispatch('pushchList', this.ListDetails)
		},
		searchByName(h){
			if(this.inputFlag){
				return
			}
			if(!h){
				this.page = 1
			}
			if(this.search){
				this.$post('Prescription/Medication/GetTCMMedicationByName',this.$pick({ 
					MedicationName: this.search,
					MedicationForm:this.subId,//药态（0：不限，1：饮片，2：颗粒）
					OrgId: this.user.Data.User.OrgId,
					DoctorId:this.user.Data.User.DoctorId,
					PageIndex: this.page,
					PageSize: 15
				})).then(o => {
					if (!o.ReturnData || o.ReturnData.length < 15){
						this.haveMore = false
					}
					else {
						this.haveMore = true
					}
					if (h){
						o.ReturnData.forEach(v => {
							this.searchList.push(v)
						})
					}
					else {
						this.searchList = o.ReturnData
					}
					if(this.searchList && this.searchList.length){
						setTimeout(()=>{
							this.getMore()
							this.reload()
						},100)
					}
				})
			}
		},
		getMore(){
		   const el = this.$refs.el
		   let timer = null
		   el.onscroll = () => {
		   	clearTimeout(timer)
		   	timer = setTimeout(() => {
		   		const top = el.scrollTop ||
		   			el.pageYOffset ||
		   			el.scrollTop,
		   			h = el.scrollHeight,
		   			wh = el.offsetHeight
		   		if(h - top - wh < 100 && this.haveMore) {
		   			this.page += 1
		   			this.searchByName(true);
		   		}
		   	}, 100)
		   }
		},
		reload(){
			//ios页面卡死问题
			let el = this.$refs.el
			let lastY = 0;
			el.addEventListener('touchstart', function(event) {
				lastY = event.touches[0].clientY;
			});
			//获取滚动元素
			let scrollEle =this.$refs.el
			//先移除监听事件
			scrollEle.removeEventListener('touchmove', function (event) {
				event.preventDefault();
			}, false);
			//再添加监听事件
			scrollEle.addEventListener('touchmove', function(event) {
				let top = event.touches[0].clientY;
				let scrollTop = scrollEle.scrollTop;
				let scrollHeight = scrollEle.scrollHeight;
				let clientHeight = scrollEle.clientHeight;
				let direction = (lastY - top) < 0 ? "up" : "down";
				if (scrollTop == 0 && direction == "up") {
				  event.preventDefault();
				} else if (scrollTop >= (scrollHeight - clientHeight) && direction == "down") {
				  event.preventDefault();
				}
				lastY = top;
			});
		  
		}
    }
}



